import React from 'react'
import Link from 'gatsby-link'
import {graphql} from 'gatsby'
import Img from 'gatsby-image'

import Layout from "../components/layout"
import SEO from "../components/seo"

export default function Template({data}) {
	const post = data.markdownRemark

	return(
	<Layout>
    <SEO title={post.frontmatter.title} />
    
    <section className="common">
        <div className="container">

        <div class="columns">
  <div class="column is-four-fifths">
  <button class="button is-dark is-small">{post.frontmatter.category}</button>

  <h1 className="title">{post.frontmatter.title}</h1>



			<strong>Tags: </strong><div dangerouslySetInnerHTML={{ __html: post.frontmatter.tags }} />

  </div>
  <div class="column">    
  <figure class="image is-128x128">
      <Img fluid={post.frontmatter.image.childImageSharp.fluid} alt="" />
      </figure>
</div>
</div>

<div className="column single-description">
<div dangerouslySetInnerHTML={{ __html: post.html }} />
</div>


<div className="column">
<Link to="/" className="button is-link">Back</Link>
</div>
       
        </div>
    </section>
    
		

  </Layout>

		
	)
}

export const postQuery = graphql`
	query BlogPostByPath($path:String!){
		markdownRemark(frontmatter: {path: {eq: $path}}){
			html
			frontmatter {
				path
				title
				author
				date
				tags
				category
				image{
                    childImageSharp{
                        fluid{
                          ...GatsbyImageSharpFluid
                        }
                      }
                }
			}
		}
	}
`














